*{
  background-color: honeydew;
  background-image: pm
}
.App {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
 
}

.App-title {
  text-align: center;
  font-size: 250%;
}
.demo{
  font-size: 22px;
}

.logo{
  animation: App-logo-spin 2s linear 1s infinite ;
}
.text-input {
  width: 100%;
  border: 1px solid #f7f7f7;
  padding: 10px;
}

.text-input:hover {
  box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.25);
}

.news-items {
  list-style: none;
  padding-left: 0;
  text-decoration: none;
  font-size: 160%;
  background-color: rgb(255,255,255);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 15px 0 rgba(0, 0, 0, 0.19);
  color: black;
  font-size: 20px;
  margin-bottom: 10px;
  padding: 10px;
  text-decoration: none;
}
.item{
  list-style-type: none;
  padding: 0px;
  
  
  

}
.item li {
  margin-bottom: 5px;
  background-color: rgb(255,255,255);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 15px 0 rgba(0, 0, 0, 0.19);
  padding: 10px;
}
.item a {
  background-color: rgb(255,255,255);
  color: black;
  font-size: 20px;
  margin-bottom: 10px;
  font-style: italic;
  
  text-decoration: none;
}

.item:hover {
  transform-origin: bottom;
  transition: all ease 0.3s;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
